import { gsap } from "gsap";
import Swiper from "swiper/bundle";
import "swiper/swiper-bundle.css";
import "../scss/fontawesome.scss";
import "../scss/main.scss";
/* -------------------------------!!!!!!!!!!!!!!!!! custom cursor !!!!!!!!!!!!!!!!!!!!------------------- */
const cursor = document.querySelector(".cursor");
document.body.addEventListener("mousemove", (e) => {
  let xPos = e.clientX;
  let yPos = e.clientY;
  cursor.style.left = `${xPos}px`;
  cursor.style.top = `${yPos}px`;
});
/* !!!!!!!!!!!!!!!!!!!!!=========================right carousel=======================!!!!!!!!!!!!!!!!! */
const rightCarousel = new Swiper(".carousel", {
  loop: true,
  slidesPerView: 3,
  spaceBetween: 30,
  grabCursor: true,
  speed: 1000,
  navigation: {
    nextEl: ".middle .next",
    prevEl: ".middle .prev",
  },
  on: {
    slideChangeTransitionStart: function () {
      let swiper = this;
      let slides = swiper.slides;
      slides.forEach((slide, index) => {
        let imgWrapper = slide.querySelector(".img-wrapper");
        imgWrapper.style.transform = `scale(1.2)`;
      });
    },
    slideChangeTransitionEnd: function () {
      let swiper = this;
      let slides = swiper.slides;
      slides.forEach((slide, index) => {
        let imgWrapper = slide.querySelector(".img-wrapper");
        imgWrapper.style.transform = `scale(1)`;
      });
    },
  },
});
const mainCarousel = new Swiper(".main", {
  loop: true,
  slidesPerView: 1,
  watchSlidesProgress: true,
  speed: 1000,
  grabCursor: true,
  navigation: {
    nextEl: ".middle .next",
    prevEl: ".middle .prev",
  },
  pagination: {
    el: ".middle .pagination",
    type: "fraction",
  },
  on: {
    slideChangeTransitionStart: function () {
      let swiper = this;
      let slides = swiper.slides;
      slides.forEach((slide, index) => {
        let container = slide.querySelector(".container");
        container.style.transform = `scale(1.2)`;
      });
    },
    slideChangeTransitionEnd: function () {
      let swiper = this;
      let slides = swiper.slides;
      slides.forEach((slide, index) => {
        let container = slide.querySelector(".container");
        container.style.transform = `scale(1)`;
      });
    },
  },
});
/* =====================animation============================== */
let textWrapper = document.querySelector(".title-1");
textWrapper.innerHTML = textWrapper.textContent.replace(
  /\S/g,
  "<span class='letter'>$&</span>"
);

let textWrapper2 = document.querySelector(".title-2");
textWrapper2.innerHTML = textWrapper2.textContent.replace(
  /\S/g,
  "<span class='letter'>$&</span>"
);
console.log(textWrapper);
const tl = new gsap.timeline({
  defaults: {
    duration: 0.8,
  },
});
window.addEventListener("DOMContentLoaded", () => {
  tl.to(".preloader", {
    opacity: 0,
    duration: 0.5,
    delay: 5,
  });
  tl.to(".middle .overlay-2", {
    clipPath: "polygon(100% 0, 100% 0, 100% 100%, 100% 100%)",
    ease: "back",
  });
  tl.to(
    ".middle .overlay-1",
    {
      clipPath: "polygon(100% 0, 100% 0, 100% 100%, 100% 100%)",
      ease: "back",
    },
    "-=0.1"
  );
  tl.to(
    ".carousel .swiper-slide .img-wrapper",
    {
      duration: 1,
      clipPath: "polygon(0 0, 100% 0, 100% 100%, 0 100%)",
      scale: 1,
      ease: "back",
    },
    "-=1.1"
  );

  tl.from(
    ".main .container",
    {
      scale: 1.5,
    },
    "-=0.5"
  );
  tl.to(
    ".title-1 .letter",
    {
      y: "0",
      clipPath: "polygon(0 0, 100% 0, 100% 100%, 0 100%)",
      stagger: ".03",
    },
    "+=0.5"
  );

  tl.to(
    ".title-2 .letter",
    {
      y: "0",
      clipPath: "polygon(0 0, 100% 0, 100% 100%, 0 100%)",
      stagger: ".03",
    },
    "-=.7"
  );
  tl.from(".muted", {
    opacity: 0,
    stagger: 0.1,
    y: 20,
  });
  tl.from(
    ".social-wrapper i",
    {
      opacity: 0,
      y: 40,
      stagger: 0.1,
    },
    "-=0.9"
  );
});
